.form-user.container {
  padding-top: 42px;
  padding-left: 10.4vw;
  padding-right: 10.4vw;
  padding-bottom: 64px;

  @media (max-width: 1280px) {
    padding-left: 3.6vw;
    padding-right: 3.6vw;
  }
}

.form-user__present {
  display: grid;
  grid-template-columns: 46.8vw 1fr;
  column-gap: 2vw;
  align-items: start;
  margin-bottom: 4.43vw;

  @media (max-width: 1080px) {
    grid-template-columns: 1fr;
  }

  @media (max-width: 767px) {
    text-align: center;
  }
}

.form-user__present h3 {
  margin: 0;

  @media (max-width: 1280px) {
    margin-bottom: 23px;
  }
}

.form-user__present p {
  margin: 0;
}

.form-user__filter {
	max-width: 1336px;
  margin-top: 90px;

  @media (max-width: 1280px) {
    max-width: 1110px;
  }

  @media (max-width: 1080px) {
    max-width: 890px;
  }

  @media (max-width: 992px) {
    max-width: 100%;
  }
}

.form-user__filter {
  font-size: clamp(25px, 2.3vw, 34px);
  line-height: 34px;
  font-variation-settings: "wght" 400, "wdth" 100;
  color: var(--back-main);
  margin: 0;

  @media (max-width: 1280px) {
    font-size: 23px;
  }

  @media (max-width: 480px) {
    font-size: 21px;
  }
}

.form-user__field {
	padding: 0;
	margin: 0;
	margin-bottom: 58px;
	border: none;
}

.form-user__filter input[type="checkbox"] + label,
.form-user__filter input[type="radio"] + label {
  cursor: pointer;
}

.form-user__filter input,
.form-user__filter textarea {
  font-size: 20px;
  color: var(--back-input);
}

.form-user__note.text-site {
  font-size: clamp(20px, 1.3vw, 25px);
  line-height: 28px;
  margin: 0;
  margin-bottom: 10px;

  @media (max-width: 480px) {
    text-align: center;
  }
}

.form-user__note a {
  color: var(--back-main);
}

.form-user__btn {
  display: block;
  font-size: clamp(22px, 1.75vw, 33px);
  font-variation-settings: "wght" 400, "wdth" 100;
  color: var(--basic-white);
  margin-left: auto;

  @media (max-width: 992px) {
    margin: 30px auto 0;
  }
}

.form-user--order {
  padding-left: 6.8vw;
  padding-bottom: 4.43vw;

  @media (max-width: 1280px) {
    padding-left: 0;
  }

  @media (max-width: 992px) {
    padding-bottom: 64px;
  }
}

.form-user--order .title-explanation span {
  display: block;
  color: var(--basic-dark-pink);
}

.form-user--order form {
  margin-top: 4.5vw;
}

.form-user__sum {
  display: grid;
  grid-template-columns: max-content 1fr;
  row-gap: 3vw;
  align-items: center;
  margin-top: 2.8vw;

  @media (max-width: 992px) {
    grid-template-columns: 1fr;
    text-align: center;
  }
}

.form-user__sum p > span {
  font-variation-settings: "wght" 400, "wdth" 95;
  color: var(--basic-dark-pink);

  @media (max-width: 580px) {
    display: block;
    margin-top: 15px;
  }

  @media (max-width: 480px) {
    margin-top: 10px;
  }

  @media (max-width: 380px) {
    margin-top: 5px;
  }
}

.form-user--order .form-user__note a {
  text-decoration: underline;
}

.form-user--order .field-profiles {
  grid-template-columns: repeat(3, 1fr);

  @media (max-width: 1080px) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: 992px) {
    grid-template-columns: 1fr;
  }
}

input.form-user__btn {
  font-size: clamp(22px, 1.75vw, 33px);
  color: var(--basic-white);
}

.form-user--order .form-user__btn {
  @media (max-width: 992px) {
    margin-top: 0;
  }
}
