.products-gallery {

}

.products-gallery--goods {
  padding-left: 6.8vw;
  margin-bottom: 3.75vw;

  @media (max-width: 992px) {
    padding-left: 0;
    margin-bottom: 6vw;
  }

  @media (max-width: 680px) {
    margin-bottom: 8vw;
  }
}

.products-gallery__list {
  padding-top: 2.24vw;
}

.products-gallery__card {
  display: grid;
  justify-content: center;
}

.products-gallery__card--left {
  padding-left: 10.4vw;

  @media (max-width: 1080px) {
    padding-left: 3.6vw;
  }

  @media (max-width: 767px) {
    padding-left: 10.4vw;
    padding-right: 10.4vw;
  }

  @media (max-width: 680px) {
    padding-left: 0;
    padding-right: 0;
  }
}

.products-gallery__card--right {
  padding-right: 10.4vw;

  @media (max-width: 1080px) {
    padding-right: 3.6vw;
  }

  @media (max-width: 767px) {
    padding-left: 10.4vw;
    padding-right: 10.4vw;
    order: -1;
  }

  @media (max-width: 680px) {
    padding-left: 0;
    padding-right: 0;
  }
}

.products-gallery__item {
  margin-bottom: 4.84vw;
}

.products-gallery--idea h2 {
  padding-left: 10.4vw;

  @media (max-width: 1080px) {
    padding-left: 3.6vw;
  }

  @media (max-width: 767px) {
    text-align: center;
    padding-right: 3.6vw;
  }
}

.products-gallery--goods h2 {
  @media (max-width: 992px) {
    text-align: center;
  }
}

.products-gallery p {
  font-family: inherit;
  font-size: 33px;
  font-variation-settings: "wght" 400,"wdth" 90;
  margin: 0;
  padding-top: 2.08vw;
  padding-bottom: 3.59vw;

  @media (max-width: 1600px) {
    font-size: 27px;
  }

  @media (max-width: 1280px) {
    font-size: 22px;
  }

  @media (max-width: 1080px) {
    font-size: 20px;
    padding-bottom: 6vw;
  }

  @media (max-width: 992px) {
    font-size: 21px;
  }

  @media (max-width: 767px) {
    padding-bottom: 8vw;
  }

  @media (max-width: 680px) {
    text-align: center;
  }

  @media (max-width: 480px) {
    padding-left: 3.6vw;
    padding-right: 3.6vw;
  }
}

.products-gallery .gallery-card__item {
  max-width: 346px;
  text-decoration: none;
  margin: 0 auto;
}

.products-gallery .info-card {
  margin-top: 1.7vw;

  @media (max-width: 767px) {
    margin-top: 3.9vw;
  }
}

.products-gallery .info-card p {
  font-size: clamp(25px, 1.4vw, 25px);
  line-height: 24px;
  padding: 0;
  margin-top: 1.25vw;
}

  @media (max-width: 992px) {
    font-size: 25px;
  }

.products-gallery__img {
  position: relative;
  width: 49.4vw;
  height: 46.2vw;

  @media (max-width: 767px) {
    width: 100%;
    height: 100%;
    margin: 0 auto;
  }
}

.products-gallery__img img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

.products-gallery__direct {
  width: 100%;
  display: flex;
  justify-content: space-between;

@media (max-width: 1080px) {
    width: 432px;
    margin: 0 auto;
  }

@media (max-width: 767px) {
    width: 50%;
    margin: 0 auto;
  }
}

.products-gallery__direct li {
  display: flex;
  justify-content: space-between;
}

.products-gallery__item {
  display: flex;
  align-items: start;
  color: var(--back-main);
  text-decoration: none;
  margin-bottom: 0;
}

.products-gallery__direct p {
  font-size: clamp(20px, 1.4vw, 25px);
  padding: 0;
}

.products-gallery__direct img {
  width: 13px;
  height: 21px;
  margin-right: 32px;
}

.products-gallery__direct li:nth-child(2) img {
  margin-right: 0;
  margin-left: 32px;
  transform: rotate(180deg);

@media (max-width: 767px) {
  }
}

@media (max-width: 1080px) {
    .products-gallery__direct p {
      font-size: 0;
  }
}

.products-gallery__list li {

}
