.product-order {

}

.product-order__count {

}

.product-order__count input[type="number"] {
  display: inline-block;
  width: 67px;
  height: 33px;
  font-size: clamp(20px, 1.4vw, 25px);
  font-variation-settings: "wght" 700, "wdth" 90;
  color: var(--basic-green);
  text-align: center;
  border: 1px solid var(--back-main);
  padding-left: 6px;

  @media (max-width: 1080px) {
    width: 60px;
  }
}

.product-order__count p {
  margin-top: 8px;
}

.product-order__count.info-card {
  padding-left: 0;
  padding-right: 0;
}

.product-order__count .info-card__desc {
  margin-top: 23px;
}

.product-order article {
  position: relative;
}

.product-order__btn {
  position: absolute;
  width: 35px;
  height: 35px;
  top: -1.5vw;
  right: -1.04vw;
  background: url("../img/btn-close.png") transparent;
  background-repeat: no-repeat;
  background-size: 100% auto;
  border: none;
  cursor: pointer;

  @media (max-width: 1280px) {
    background-size: 75% auto;
  }

  @media (max-width: 992px) {
    background-size: 80% auto;
  }

  @media (max-width: 680px) {
    right: 1.04vw;
  }
}

.product-order__btn span {
  visibility: hidden;
}

.product-order__sum {
  display: flex;
  align-items: baseline;
  padding-left: 6.8vw;
  margin-top: 5.3vw;
  margin-bottom: 5.3vw;

  @media (max-width: 1080px) {
    flex-direction: column;
  }

  @media (max-width: 992px) {
    padding-left: 0;
  }
}

.product-order__sum p > span {
  font-variation-settings: "wght" 400, "wdth" 95;
  color: var(--basic-dark-pink);
}

.product-order__sum p {
  font-variation-settings: "wght" 700, "wdth" 90;
  margin: 0;
  margin-right: 2vw;

  @media (max-width: 1080px) {
    margin-right: 0;
    margin-bottom: 35px;
  }

  @media (max-width: 767px) {
    text-align: center;
    margin: 0 auto;
    margin-bottom: 35px;
  }

  @media (max-width: 580px) {
    font-size: clamp(32px, 9vw, 38px);
    line-height: 42px;
  }
}

.product-order__sum a {
  position: relative;
  display: inline-block;
  font-size: clamp(22px, 1.75vw, 33px);
  height: 2.5vw;
  margin-left: 3vw;

  @media (max-width: 1080px) {
    height: 35px;
    margin-left: 52px;
  }

  @media (max-width: 767px) {
    margin-left: calc(50% - 110px / 2);
    margin-bottom: 35px;
  }
}

.product-order__sum a::before {
  position: absolute;
  content: "";
  width: 32px;
  height: 44px;
  left: -3vw;
  background-image: url("../img/autorenew.svg");
  background-repeat: no-repeat;
  background-size: 100% auto;

  @media (max-width: 1600px) {
    background-size: 85% auto;
  }

  @media (max-width: 1280px) {
    background-size: 65% auto;
  }

  @media (max-width: 1080px) {
    left: -52px;
    background-size: 100% auto;
  }
}

.form-user__sum .caption {
  font-variation-settings: "wght" 700, "wdth" 90;
}
