.page-footer__right {
  text-align: right;
  padding-top: 48px;
  padding-left: 10.4vw;
  padding-right: 10.4vw;
  padding-bottom: 48px;

  @media (max-width: 992px) {
    text-align: center;
    padding-left: 3.6vw;
    padding-right: 3.6vw;
  }
}

.page-footer__right.text-site {
  @media (max-width: 1280px) {
    font-size: 17px;
  }
}

.page-footer__right a {
  @media (max-width: 580px) {
    display: inline-block;
  }
}
