.field-comment {
  margin-bottom: 3.59vw;
}

.field-comment textarea {
  width: 100%;
  height: 155px;
  padding: 20px;
  border: 2px solid var(--back-input);
  margin-top: 18px;
  resize: vertical;

  @media (max-width: 1600px) {
    height: 130px;
  }

  @media (max-width: 1280px) {
    height: 100px;
  }
}

.field-comment legend {
  @media (max-width: 480px) {
    text-align: center;
  }
}
