.sidenav {
  padding-left: 6.8vw;
  margin-bottom: 8.5vw;

  @media (max-width: 580px) {
    padding-left: 0;
    padding-bottom: 8.6vw;
    margin: 0 auto;
  }
}

.sidenav a {
  display: block;
  font-size: 25px;
  font-variation-settings: "wght" 700, "wdth" 100;
  color: #2b2a29;
  text-decoration: none;
}

.sidenav__item:nth-child(1) > a {
  margin-top: 0;
}

.sidenav__item > a {
  margin-top: clamp(25px, 4vw, 49px);
  margin-bottom: 13px;
}

.sidenav__inner a {
  font-size: 20px;
  line-height: 24px;
  font-variation-settings: "wght" 400, "wdth" 100;
  margin-bottom: clamp(8px, 1vw, 15px);
}

a.sidenav__link-active {
  font-variation-settings: "wght" 700, "wdth" 100;
  color: var(--back-link);
  text-decoration: underline;
}

a.sidenav__link-active--yellow {
  color: var(--basic-yellow);
}

.sidenav__item > a {
  margin-top: clamp(25px, 4vw, 49px);
  margin-bottom: 13px;
}

.sidenav__inner ul {
  padding-bottom: 20px;
}
