.user-slider {
  position: relative;
}

.user-slider__list.swiper-wrapper {
  width: 100%;
  height: 46.3vw;
}

.user-slider__item.swiper-slide {
  display: flex;
  max-width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}

.user-slider__toggle {
  position: absolute;
  width: 37px;
  height: 37px;
  bottom: 20px;
  border: none;
  cursor: pointer;
  z-index: 1;

  @media (max-width: 680px) {
    display: none;
  }
}

.user-slider__toggle--prev {
  right: 223px;
  transform: scaleX(-1);

  @media (max-width: 992px) {
   right: 52%;
  }
}

.user-slider__toggle--next {
  right: 170px;

  @media (max-width: 992px) {
   left: 52%;
  }
}

.user-slider__toggle::before {
  position: absolute;
  content: ">";
  font-size: 47px;
  line-height: 37px;
  color: #000000;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(255, 255, 255, 0.8);
  padding-left: 10px;
  padding-right: 10px;
}

.user-slider__toggle.swiper-button-disabled {
  opacity: 0.5;
}


