.goods {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 63px;
  margin-top: 72px;
  margin-bottom: 72px;

  @media (max-width: 992px) {
    grid-template-columns: 1fr;
  }
}

.goods__gallery {
  width: 41.6vw;

  @media (max-width: 992px) {
    width: 65vw;
  }

  @media (max-width: 767px) {
    width: 80vw;
  }
}

.goods .products-gallery__list {
  padding: 0;

  @media (max-width: 992px) {
    margin: 0 auto;
  }
}

.goods__property {
  @media (max-width: 992px) {
    margin-top: 5vw;
  }
}
