.card-delivery {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  background: transparent;
  background-clip: border-box;
}

.card-delivery__header {
  padding: 0;
  margin-bottom: 0;
  background: transparent;
}

.card-delivery .site-link {
  display: inline-block;
  margin: 0;
  margin-left: 50px;

  @media (max-width: 1280px) {
    font-size: 16px;
  }

  @media (max-width: 1080px) {
    font-size: 18px;
  }
}

.card-delivery__header button {
  display: inline-block;
  font-family: inherit;
  font-size: 1.09vw;
  text-align: left;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;

  @media (max-width: 1600px) {
    font-size: 18px;
  }

  @media (max-width: 1080px) {
    font-size: 21px;
  }
}

.card-delivery__body {
  display: none;
  max-width: 300px;
  height: 480px;
  margin-top: 20px;

  @media (max-width: 1600px) {
    height: 510px;
  }

  @media (max-width: 1280px) {
    height: 480px;
  }
}

.card-delivery__body iframe {
  width: 100%;
  height: 100%;
  border-width: 0;
  outline-width: 0;
}

.card-delivery__header:focus-within .card-delivery__body {
  display: block;
}
