.property-goods {
  position: relative;
}

p.property-goods__text {
  font-size: clamp(20px, 1.4vw, 25px);
  padding: 0;
}

.property-goods__text p:last-child {
  display: flex;
}

.property-goods select {
  display: inline-block;
  width: 187px;
  height: 33px;
  font-family: inherit;
  font-size: clamp(20px, 1.4vw, 25px);
  margin-left: 20px;
  border: 1px solid var(--back-main);
  padding-left: 10px;
}

.property-goods option {

}

.property-goods option:checked {

}

.property-goods__content {
  @media (max-width: 680px) {
    text-align: center;
  }
}

.property-goods__content p {
  font-size: clamp(22px, 1.75vw, 33px);
  font-variation-settings: "wght" 400,"wdth" 100;
  color: var(--back-main);
  padding: 0;
  margin: 0;
  margin-bottom: 2.6vw;

  @media (max-width: 580px) {
    margin-bottom: 20px;
  }
}

.property-goods__content a {
  display: inline-block;
  font-size: clamp(28px, 2vw, 42px);
  font-variation-settings: "wght" 400,"wdth" 100;
  color: var(--back-main);
  text-decoration: none;
  border-bottom: 4px solid var(--basic-green);
}

.property-goods__samples {
  display: flex;
  margin-top: 23px;
  margin-bottom: 35px;
}

.property-goods__samples li {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 53px;
  height: 53px;
  margin-right: 7px;
  cursor: pointer;
}

.property-goods__samples label {
  position: relative;
}

.property-goods__samples input[type="radio"] + label::before {
  position: absolute;
  content: "";
  width: 42px;
  height: 42px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.property-goods__samples input + label {
  cursor: pointer;
}

.property-goods__samples input[type="radio"]:checked + label::after {
  position: absolute;
  content: "";
  width: 53px;
  height: 53px;
  border: 1px solid var(--basic-dark-pink);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.property-goods__samples input[type="radio"]:checked + label::after {
  width: 53px;
  height: 53px;
  border: 1px solid var(--basic-dark-pink);
}

.property-goods__samples li:nth-child(1) input[type="radio"] + label::before {
  background-color: var(--back-main);
}

.property-goods__samples li:nth-child(2) input[type="radio"] + label::before {
  background-color: transparent;
  border: 1px solid #adadad;
}

.property-goods__samples li:nth-child(3) input[type="radio"] + label::before {
  background-color: #dfccc0;
}

.property-goods .promotion {
  text-align: center;
  margin-top: -30px;
}

.property-goods .promotion > p {
  position: absolute;
  width: 100%;
  bottom: 55%;
  left: 50%;
  transform: translateX(-50%);
  font-family: inherit;
  font-size: clamp(20px, 1.5vw, 30px);
  font-variation-settings: "wght" 400, "wdth" 100;
  color: var(--back-main);
  text-align: center;
  margin: 0;
  z-index: 20;
  padding: 0;
}

.property-goods__content .info-card {
  position: relative;
  display: flex;
  padding-left: 1.8vw;
  padding-right: 0;
  margin-top: 2.7vw;
  z-index: 10;

  @media (max-width: 1280px) {
    flex-direction: column;
    padding-left: 0;
  }

  @media (max-width: 680px) {
    margin-top: 5vw;
  }
}

.property-goods__content .info-card p {
  font-size: clamp(22px, 1.75vw, 33px);
  line-height: 48px;
  text-align: left;
  margin: 0;

  @media (max-width: 680px) {
    text-align: center;
  }
}

.property-goods__content .info-card span {
  font-size: 50px;
  text-align: left;

  @media (max-width: 1600px) {
    font-size: 48px;
  }

  @media (max-width: 1280px) {
    font-size: 42px;
  }
}

a.property-goods__link {
  display: block;
  width: 270px;
  min-height: 64px;
  font-family: inherit;
  font-size: clamp(22px, 1.75vw, 33px);
  font-variation-settings: "wght" 400, "wdth" 100;
  line-height: 34px;
  color: var(--basic-white);
  background-color: var(--basic-green);
  margin-left: auto;
  padding-top: 10px;
  padding-left: 15px;
  padding-right: 15px;
  padding-bottom: 10px;
  cursor: pointer;

  @media (max-width: 1280px) {
    width: 180px;
    min-height: 42px;
    line-height: 24px;
    margin-top: 1.5vw;
    margin-left: 0;
  }

  @media (max-width: 680px) {
    margin: 0 auto;
  }
}
