.user-nav {
  display: grid;
  grid-template-columns: repeat(2, max-content);
  column-gap: 1.8vw;
  justify-content: end;
  align-items: center;

  @media (max-width: 1080px) {
    justify-content: center;
  }

  @media (max-width: 680px) {
    column-gap: 4vw;
    padding-top: 10px;
  }

  @media (max-width: 480px) {
    justify-content: end;
  }
}

.user-nav__tel {
  display: grid;
  font-size: clamp(16px, 1.3vw, 25px);
  line-height: 28px;
  color: var(--basic-white);
  text-decoration: none;

  @media (max-width: 1280px) {
    font-size: 18px;
  }

  @media (max-width: 1080px) {
    font-size: 21px;
  }

  @media (max-width: 680px) {
    font-size: 25px;
    padding-left: 0;
  }

  @media (max-width: 580px) {
    justify-content: center;
    padding-left: 0;
  }

  @media (max-width: 480px) {
    font-size: 23px;
    justify-self: start;
    padding-right: 9vw;
  }

  @media (max-width: 380px) {
    font-size: 21px;
  }
}

.user-nav__cart {
  position: relative;

  @media (max-width: 480px) {

  }
}

.user-nav__cart a {
  text-decoration: none;
}

.user-nav__cart img {
  width: 28px;
  height: 28px;
  object-fit: cover;
}

.user-nav__cart::before {
  position: absolute;
  content: "";
  width: 30px;
  height: 30px;
  top: -8px;
  left: 20px;
  background-image: url("../img/color-circle.svg");
  background-repeat: no-repeat;
}

.user-nav__cart span {
  position: relative;
  font-family: inherit;
  font-size: 18px;
  font-variation-settings: "wght" 400, "wdth" 100;
  color: var(--basic-white);
  top: -15px;
  right: 2px;
}
