.slider-main__toggle {
  position: absolute;
  width: 37px;
  height: 37px;
  bottom: 20px;
  border: none;
  cursor: pointer;
  z-index: 1;

  @media (max-width: 680px) {
    display: none;
  }
}

.slider-main__toggle--prev {
  right: 223px;
  transform: scaleX(-1);

  @media (max-width: 992px) {
   right: 52%;
  }
}

.slider-main__toggle--next {
  right: 170px;

  @media (max-width: 992px) {
   left: 52%;
  }
}

.slider-main__toggle::before {
  position: absolute;
  content: ">";
  font-size: 47px;
  line-height: 37px;
  color: #000000;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(255, 255, 255, 0.8);
  padding-left: 10px;
  padding-right: 10px;
}

.slider-main__toggle.swiper-button-disabled {
  opacity: 0.5;
}



