.button {
  width: 270px;
  min-height: 64px;
  font-family: inherit;
  font-size: clamp(22px, 1.75vw, 33px);
  font-variation-settings: "wght" 400, "wdth" 100;
  color: var(--basic-white);
  background-color: var(--back-main);
  border: none;
  padding-top: 10px;
  padding-left: 15px;
  padding-right: 15px;
  padding-bottom: 10px;
  cursor: pointer;

  @media (max-width: 1280px) {
    width: 180px;
    min-height: 42px;
  }
}
