.orders {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 63px;
  padding-left: 6.8vw;
  margin-top: 72px;
  margin-bottom: 72px;

  @media (max-width: 992px) {
    grid-template-columns: 1fr;
    margin-top: 50px;
  }

  @media (max-width: 680px) {
    padding-left: 6.8vw;
  }

  @media (max-width: 580px) {
    margin-top: 25px;
    margin-bottom: 25px;
  }
}
