.gallery-top {
  margin-top: 20px;
}

.gallery-top.swiper-container {
  position: relative;
  overflow: hidden;
  margin: 0 auto;
}

.gallery-top::after {
  position: absolute;
  content: "";
  width: 110px;
  height: 110px;
  background-image: url("../img/label-logo.svg");
  background-repeat: no-repeat;
  background-size: 100% auto;
  z-index: 20;
  right: 29px;
  bottom: 110px;
  opacity: 0.6;

  @media (max-width: 680px) {
    width: 80px;
    height: 80px;
  }

  @media (max-width: 480px) {
    width: 65px;
    height: 65px;
  }
}

.gallery-top__item.swiper-slide {
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: center;
  overflow: hidden;
}

.gallery-top.swiper-container-autoheight {
  &, .swiper-slide {
    width: auto;
    height: 50.4vw;
}

.gallery-top.swiper-container-autoheight {
  &, .swiper-slide img {
    display: block;
    width: auto;
    height: 100%;
    }
  }
}
