.field-address {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 7vw;
  row-gap: 3.3vw;

  @media (max-width: 992px) {
    grid-template-columns: 1fr;
  }

  @media (max-width: 767px) {
    text-align: center;
  }

  @media (max-width: 480px) {
    row-gap: 6vw;
  }
}

.field-address__item {
  margin: 0;
}

.field-address__item label {
  display: block;
}

.field-address__item input[type="text"],
.field-address__item input[type="tel"] {
  width: 100%;
  height: 34px;
  padding-left: 10px;
  border: none;
  border-bottom: 2px solid var(--back-input);
}
