.circle-block__item {
  position: absolute;
  width: clamp(264px, 20vw, 397px);
  z-index: 15;

  @media (max-width: 992px) {
    display: none;
  }

&::before {
  content: "";
  display: block;
  width: 48px;
  height: 48px;
  background: url("../img/marker-circle.png"), transparent;
  background-repeat: no-repeat;
  background-size: 100% auto;
  border: none;
  cursor: pointer;
  z-index: 20;

  @media (max-width: 1600px) {
    background-size: 80% auto;
  }

  @media (max-width: 1280px) {
    background-size: 65% auto;
  }

  @media (max-width: 992px) {
    background-size: 55% auto;
  }
}
}

div.circle-block__card--bottom {
  top: -2vw;
  left: 2.5vw;
}

div.circle-block__card--top {
  top: -12vw;
}

div.circle-block__card--left {
  left: -87%;
}

.circle-block__card {
  position: absolute;
  display: none;
  width: clamp(230px, 18vw, 346px);
  top: -5vw;
  left: 2.5vw;
  background-color: rgba(255, 255, 255, 0.8);
  text-align: center;
  padding: 20px;
  padding-bottom: 30px;
}

.circle-block__info.info-card {
  text-align: center;
  padding-top: 1vw;
  padding-left: 3.1vw;
  padding-right: 3.1vw;
  margin-top: 0;

  @media (max-width: 1600px) {
    padding: 0;
  }
}

.circle-block__info.info-card p {
  margin-top: 1.2vw;
}

.circle-block__info.info-card a {
  display: inline-block;
  font-size: clamp(20px, 1.4vw, 25px);
  border-bottom: 4px solid var(--basic-green);
  margin-top: 1.4vw;
}

.circle-block__item:hover .circle-block__card,
.circle-block__item:focus-within .circle-block__card,
.circle-block__item:active .circle-block__card {
  display: block;
}
