.user-order {
  position: relative;
}

@media (max-width: 992px) {
  .user-order:last-child {
    margin-top: 7vw;
  }
}

.user-order p {
  font-size: clamp(22px, 1.75vw, 33px);
  font-variation-settings: "wght" 400,"wdth" 100;
  color: var(--back-main);
  padding: 0;
  margin: 0;
  margin-bottom: 2vw;
  padding: 0;

  @media (max-width: 580px) {
    margin-bottom: 20px;
  }
}

p.user-order__text {
  font-size: clamp(20px, 1.4vw, 25px);
}

.user-order__text p:last-child {
  display: flex;
}

p.user-order__select {
  color: var(--basic-dark-pink);
}

.user-order__content p {
  font-size: clamp(22px, 1.75vw, 33px);
  font-variation-settings: "wght" 400,"wdth" 100;
  color: var(--back-main);
  padding: 0;
  margin: 0;
  margin-bottom: 2.6vw;
}

.user-order a {
  font-size: clamp(28px, 2vw, 42px);
  font-variation-settings: "wght" 400,"wdth" 100;
  color: var(--back-main);
  text-decoration: none;
  border-bottom: 4px solid var(--basic-green);
}

.user-order .info-card {
  position: relative;
  display: flex;
  flex-shrink: 0;
  align-items: center;
  padding-left: 0;
  margin-top: 0;
  z-index: 10;

  @media (max-width: 1280px) {
    flex-direction: column;
    align-items: start;
    padding-left: 0;
  }

  @media (max-width: 992px) {
    flex-direction: row;
  }

  @media (max-width: 580px) {
    flex-direction: column;
  }
}

.user-order .info-card p {
  font-size: clamp(22px, 1.75vw, 33px);
  line-height: 48px;
  text-align: left;
  margin: 0;
}

.user-order .info-card span {
  font-size: 50px;
  text-align: left;

  @media (max-width: 1600px) {
    font-size: 48px;
  }

  @media (max-width: 1280px) {
    font-size: 42px;
  }
}

.user-order button {
  margin-left: 4.17vw;

  @media (max-width: 1280px) {
    margin-left: 0;
  }

  @media (max-width: 992px) {
    margin-top: 0;
    margin-left: 4.17vw;
  }

  @media (max-width: 580px) {
    margin-left: 0;
  }
}

.user-order ol {
  font-family: inherit;
  font-size: clamp(20px, 1.4vw, 25px);
  color: var(--basic-dark-pink);
  list-style-position:inside;
  padding: 0;
  margin: 0;
  margin-top: 2.45vw;

  @media (max-width: 580px) {
    margin-top: 7vw;
  }
}

.user-order li {
  margin-bottom: 0.42vw;
}
