.field-delivery__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 2.3vw;
  row-gap: 25px;
  margin-top: 25px;

  @media (max-width: 1080px) {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 3.5vw;
    row-gap: 42px;
  }

  @media (max-width: 680px) {
    grid-template-columns: 1fr;
  }
}

.field-delivery__item {
  display: grid;
  align-content: start;
}

.field-delivery__item label {
  position: relative;
  font-size: clamp(18px, 1.3vw, 23px);
  line-height: 28px;
  padding-left: 50px;

  @media (max-width: 1080px) {
    font-size: 21px;
    line-height: 23px;
  }
}

.field-delivery__item input[type="radio"] + label::before {
  position: absolute;
  content: "";
  width: 35px;
  height: 35px;
  top: -6px;
  left: 0;
  background: transparent;
  border: 1px solid var(--back-input);
  border-radius: 50%;
}

.field-delivery__item input[type="radio"]:checked + label::before {
  position: absolute;
  content: "";
  width: 35px;
  height: 35px;

  left: 0;
  border: 1px solid var(--back-input);
  background: url("../img/marker-circle-nav.png");
  background-repeat: no-repeat;
  background-size: 25px 25px;
  background-position: 50% 50%;
  z-index: 10;
}

.field-delivery__item a {
  color: var(--back-main);
  text-decoration: underline;

  @media (max-width: 580px) {
    display: block;
  }
}

