.business {
  background-color: var(--back-main);
}

.business__list.container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, max-content));
  column-gap: 4.9vw;
  row-gap: 60px;
  padding-top: 45px;
  padding-left: 10.4vw;
  padding-right: 10.4vw;
  padding-bottom: 57px;

  @media (max-width: 1280px) {
    padding-left: 3.6vw;
    padding-right: 3.6vw;
  }

  @media (max-width: 767px) {
    grid-template-columns: 1fr;
    row-gap: 40px;
    text-align: center;
  }

  @media (max-width: 480px) {
    row-gap: 30px;
  }
}

.business__list p,
.business__list a {
  font-size: clamp(20px, 1.3vw, 25px);
  color: var(--basic-white);
  margin: 0;

  @media (max-width: 1280px) {
    font-size: 17px;
  }
}

.business__list p {
  margin-bottom: 1.82vw;

  @media (max-width: 1280px) {
    margin-bottom: 20px;
  }
}

p.business__copyright {
  font-size: clamp(23px, 1.7vw, 34px);
  font-variation-settings: "wght" 400, "wdth" 100;
  margin-bottom: 2.6vw;

  @media (max-width: 1280px) {
    font-size: 23px;
    margin-bottom: 37px;
  }
}

.business__item:nth-child(3) {
  padding-left: 7.4vw;

  @media (max-width: 1280px) {
    padding-left: 0;
  }
}

p.business__title,
p.business__tel {
  font-size: clamp(23px, 1.8vw, 36px);
}

p.business__title {
  margin-bottom: 2.6vw;

  @media (max-width: 992px) {
    margin-bottom: 27px;
  }
}

a.business__tel {
  display: block;
  line-height: 20px;
  text-decoration: none;
}

.business__info a {
  line-height: 20px;
  font-weight: 100;
}
