.catalog {
  padding-top: 38px;
}

.catalog--inner,
.catalog--standart,
.catalog--basket {
  padding-top: 0;
}

.catalog a {
  text-decoration: none;
}

.catalog__present {
  display: grid;
  grid-template-columns: 2fr clamp(157px, 12vw, 235px);
  grid-template-rows: min-content 1fr;
  column-gap: 2vw;
  padding-left: 6.8vw;
  padding-right: 6.8vw;

  @media (max-width: 1080px) {
    padding-left: 0;
    padding-right: 0;
  }

  @media (max-width: 767px) {
    grid-template-columns: 1fr;
    text-align: center;
  }
}

.catalog--inner .catalog__present {
  grid-template-columns: 1fr;
  padding-right: clamp(100px, 20vw, 540px);

  @media (max-width: 767px) {
    padding-right: 0;
  }
}

.catalog--col3 .catalog__present,
.catalog--about .catalog__present {
  grid-template-columns: 1fr;
  padding-left: 0;
}

.catalog__present h2 {
  @media (max-width: 767px) {
    margin-bottom: 25px;
  }
}

.catalog__present p {
  padding-right: 120px;
  margin-top: 32px;

  @media (max-width: 992px) {
    grid-template-columns: 1fr;
    padding: 0;
  }

  @media (max-width: 767px) {
    text-align: left;
    padding: 0;
  }

  @media (max-width: 680px) {
    text-align: center;
  }
}

.catalog--inner .catalog__present h2 {
  @media (max-width: 767px) {
    text-align: center;
    margin-bottom: 0;
  }
}

.catalog__cup {
  grid-column: 2/3;
  grid-row: 1/3;

  @media (max-width: 767px) {
    grid-column: 1/-1;
    grid-row: 2/3;
    margin: 0 auto;
  }
}

.catalog__list {
  margin-top: clamp(60px, 10vw, 134px);
}

.catalog--standart .gallery-card {
  margin-top: clamp(60px, 5vw, 80px);
  margin-bottom: clamp(60px, 5vw, 80px);
}

.catalog--inner .info-card {
  padding: 0;
  margin: 0;
}

.catalog--col3,
.catalog--about {
  padding-top: 0;
}

.catalog--about .catalog__present h2 {
  font-family: inherit;
  font-size: clamp(50px, 3.9vw, 75px);
  line-height: clamp(54px, 4vw, 77px);
  font-variation-settings: "wght" 400,"wdth" 95;
  color: var(--back-gray);
  margin: 0;

  @media (max-width: 992px) {
    margin-bottom: 25px;
  }
}

.catalog--about .catalog__present p {
  font-size: clamp(22px, 1.75vw, 33px);
  font-variation-settings: "wght" 400,"wdth" 100;
  color: var(--back-main);
  margin: 0;
  margin-top: 32px;

  @media (max-width: 992px) {
    margin-top: 28px;
  }
}

.catalog__present ul {
  font-family: inherit;
  font-size: clamp(20px, 1.4vw, 25px);
  line-height: 24px;
  font-variation-settings: "wght" 400,"wdth" 90;
  color: var(--back-main);
  padding-left: 45px;
  margin-top: 35px;
  text-align: left;

  @media (max-width: 767px) {
    padding-left: 30px;
  }
}

.catalog__present li {
  position: relative;
  margin-bottom: 1.04vw;

  @media (max-width: 767px) {
    margin-bottom: 0.8vw;
  }
}

.catalog__present li::before {
  position: absolute;
  content: "";
  width: 16px;
  height: 16px;
  top: 8%;
  left: -45px;
  border-radius: 50%;
  background-color: var(--back-link);

  @media (max-width: 767px) {
    width: 12px;
    height: 12px;
    left: -30px;
  }
}

.catalog--about .catalog__present > div {
  display: grid;
  grid-template-columns: 2fr clamp(157px, 12vw, 235px);
  grid-template-rows: min-content 1fr;
  column-gap: 8vw;

  @media (max-width: 992px) {
    grid-template-columns: 1fr;
  }
}

.catalog--product h2,
.catalog--standart h2,
.catalog--basket h2 {
  padding-left: 6.8vw;

  @media (max-width: 1080px) {
    padding-left: 0;
  }

  @media (max-width: 767px) {
    text-align: center;
  }
}

.content-inner__list--template li {
  position: relative;
}

.catalog--basket .gallery-card {
  margin-top: 5vw;

  @media (max-width: 580px) {
    margin-top: 35px;
  }
}

.catalog--basket h2 {
  @media (max-width: 1080px) {
    padding-left: 0;
  }

  @media (max-width: 767px) {
    text-align: center;
  }
}
